:root {
  box-sizing: border-box;
  --white: #fff;
  --dark-gray: #2f2f2f;
  font-size: calc(0.15em + 1vw);
}

*, *::before, *::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: inherit;
  color: var(--white);
  background-color: var(--dark-gray);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

#App {
  display: flex;
  justify-content: center;
  text-align:center;
  position: relative;
  left: 24em;
  top: 1em;
  width:60vw;
}

.session-length {
  max-width: 50em;
}

.break-length {
  margin-left: 3em;
  max-width: 50em;
}

.session-break-cycle, .session-break-cycle-buttonClicked {
  position: relative;
  right: 19.5em;
  top: 5em;
  /* background-color: green; */
  border-radius: 50%;
  font-size: 1.2em;
  border: 2px solid white;
  min-height: 13.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content:center ;
  margin-top: 1em;
}

.session-break-cycle-buttonClicked {
  font-size: .7em;
  min-height: 14em;
  width: 14em;
  position: relative;
  bottom: 20px;
}

.session-break-cycle button {
  margin: .5em;
}

button {
  background-color: var(--dark-gray);
  border:0;
  color:var(--white);
  font-size: .9em;
}

.minus-btn {
  margin-left: 0.35em;
}

.session-length-btn {
  display: inline;
  position: relative;
  top: 5em;
  right: 21.40em;
  margin-left: .9em;
}

.session-btn {
  right: 30.40em;
}

@media (min-width: 1366px) {
  #App {
    left:25em;
  }
}

@media (min-width: 1800px) {
  #App {
    left:26em;
  }
}

@media (max-width: 868px) {
  #App {
    left:23.5em;
    font-size: calc(0.15em + 1vw);
  }
}